import React from "react";
import Grid from "@material-ui/core/Grid";
import Chart from "react-apexcharts";
import { IconButton } from "@material-ui/core";
import SettingsIcon from "@material-ui/icons/Settings";
const ChartSection = () => {
  var options = {
    chart: {
      height: 280,
      type: "area",
      toolbar: {
        show: false,
      },
    },
    dataLabels: {
      enabled: false,
    },
    markers: {
      size: 5,
      colors: ["white"],
      strokeColor: "#00BAEC",
      strokeWidth: 3,
    },
    fill: {
      type: "gradient",
      gradient: {
        shadeIntensity: 1,
        opacityFrom: 0.7,
        opacityTo: 0.9,
        stops: [0, 90, 100],
      },
    },
    xaxis: {
      categories: [
        "01 Jan",
        "02 Jan",
        "03 Jan",
        "04 Jan",
        "05 Jan",
        "06 Jan",
        "07 Jan",
      ],
    },
  };
  var series = [
    {
      name: "DK-1",
      data: [45, 52, 38, 45, 19, 23, 2],
    },
    {
      name: "DK-2",
      data: [42, 22, 18, 65, 1, 45, 22],
    },
  ];
  return (
    <>
      <Grid container item xs={12}>
        <Grid
          container
          item
          direction="row"
          justify="space-between"
          alignItems="center"
          xs={12}
          className="m-3 mb-0"
        >
          <Grid item>
            <h6 style={{ fontWeight: "800" }}>Power Cost</h6>
          </Grid>
          <Grid item>
            <input type="date" name="date" />
            <IconButton>
              <SettingsIcon />
            </IconButton>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Chart
            options={options}
            series={series}
            type="area"
            height="400"
            className="m-3"
          />
        </Grid>
      </Grid>
    </>
  );
};

export default ChartSection;
