import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  FormControl,
  FormLabel,
  InputLabel,
  MenuItem,
  RadioGroup,
  Radio,
  Select,
  TextField,
  FormControlLabel,
  Button,
  Grid,
} from "@material-ui/core";
const useStyles = makeStyles((theme) => ({
  rounded: {
    borderRadius: "15px",
  },
  boldFont: {
    fontWeight: "600",
  },
  fullWidthField: {
    width: "100%",
  },
  radio: {
    "&$checked": {
      color: "#1a163d",
    },
  },

  checked: {},
}));

const CreateAlert = () => {
  const classes = useStyles();
  const [value, setValue] = React.useState("female");
  const [select1, setSelect1] = React.useState("");
  const [select2, setSelect2] = React.useState("");

  const handleSelect1Change = (event) => {
    setSelect1(event.target.value);
  };
  const handleSelect2Change = (event) => {
    setSelect2(event.target.value);
  };

  const handleChange = (event) => {
    setValue(event.target.value);
  };
  return (
    <>
      <div className=" p-3">
        <div className={`${classes.boldFont} h6`}>Create Alert</div>
        <TextField
          size="small"
          id="outlined-error-helper-text"
          variant="outlined"
          placeholder="Name"
          className={`${classes.fullWidthField} my-3`}
        />
        <FormControl
          component="fieldset"
          className={`${classes.fullWidthField}`}
        >
          <FormLabel component="legend">Criteria</FormLabel>
          <RadioGroup
            aria-label="criteria"
            name="criteria"
            value={value}
            onChange={handleChange}
            row
          >
            <FormControlLabel
              value="gr"
              control={
                <Radio
                  classes={{ root: classes.radio, checked: classes.checked }}
                />
              }
              label="Greater than"
            />
            <FormControlLabel
              value="lr"
              control={
                <Radio
                  classes={{ root: classes.radio, checked: classes.checked }}
                />
              }
              label="Less than"
            />
          </RadioGroup>
        </FormControl>
        <TextField
          size="small"
          variant="outlined"
          placeholder="Value"
          className={`${classes.fullWidthField} my-3`}
        />

        <FormControl
          size="small"
          variant="outlined"
          className={`${classes.fullWidthField} my-3`}
        >
          <InputLabel>Everyday</InputLabel>
          <Select
            labelId="demo-simple-select-outlined-label"
            id="demo-simple-select-outlined"
            value={select1}
            onChange={handleSelect1Change}
            label="Everyday"
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            <MenuItem value={10}>Ten</MenuItem>
            <MenuItem value={20}>Twenty</MenuItem>
            <MenuItem value={30}>Thirty</MenuItem>
          </Select>
        </FormControl>

        <FormControl
          size="small"
          variant="outlined"
          className={`${classes.fullWidthField} my-3`}
        >
          <InputLabel>Everyday</InputLabel>
          <Select
            labelId="demo-simple-select-outlined-label"
            id="demo-simple-select-outlined"
            value={select2}
            onChange={handleSelect2Change}
            label="Everyday"
          >
            <MenuItem value="">
              <em>None</em>
            </MenuItem>
            <MenuItem value={10}>Ten</MenuItem>
            <MenuItem value={20}>Twenty</MenuItem>
            <MenuItem value={30}>Thirty</MenuItem>
          </Select>
        </FormControl>
        <TextField
          size="small"
          variant="outlined"
          placeholder="Email"
          className={`${classes.fullWidthField} my-3`}
        />

        <TextField
          size="small"
          variant="outlined"
          placeholder="Phone"
          className={`${classes.fullWidthField} my-3`}
        />
              <Grid
  container
  direction="row"
  justify="space-between"
  alignItems="center"
>
        <Grid item >
        </Grid>
        <Grid item>
        <Button
              style={{ backgroundColor: "#1a163d" }}
              className="text-white px-4"
            >
              Submit
            </Button>
        </Grid>
      </Grid>
      </div>
    </>
  );
};
export default CreateAlert;
