const useStyles2 = makeStyles({
  table: {
    minWidth: 500,
  },
  rounded: {
    borderRadius: "15px",
  },
  boldText :{
    fontWeight:'600',
  }
});

export default function CustomPaginationActionsTable() {
  const classes = useStyles2();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const emptyRows =
    rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <div className={`${classes.rounded} bg-white my-3 p-2`}>
      <Grid
  container
  direction="row"
  justify="space-between"
  alignItems="center"
>
        <Grid item >
          <Button
            style={{ backgroundColor: "#1a163d" }}
            className="ml-auto text-white px-4 mx-3"
          >
            Alters
          </Button>
          <Button
            style={{ color: "#1a163d", borderColor: "#1a163d" }}
            className="border border-dark ml-auto bg-white px-4 mx-3"
          >
            Triggered Alters
          </Button>
        </Grid>
        <Grid item>
          <IconButton>
            <RefreshIcon />
          </IconButton>
        </Grid>
      </Grid>
      <TableContainer component={Paper}>
        <Table className={classes.table} aria-label="custom pagination table">
          <TableHead>
            <TableRow  >
              <TableCell className={classes.boldText}>Name</TableCell>
              <TableCell className={classes.boldText}   style={{ minWidth: "120px" }}  align="center">Price Signal</TableCell>
              <TableCell className={classes.boldText} align="center">Criteria</TableCell>
              <TableCell className={classes.boldText} align="center">Value</TableCell>
              <TableCell className={classes.boldText} align="center">Email</TableCell>
              <TableCell className={classes.boldText} align="center">Active Days</TableCell>
              <TableCell className={classes.boldText} align="center">
                Actions
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {(rowsPerPage > 0
              ? rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              : rows
            ).map((row) => (
              <TableRow key={row.email}>
                <TableCell style={{ minWidth: "100px" }} scope="row">
                  {row.name}
                </TableCell>
                <TableCell align="center">{row.priceSignal}</TableCell>
                <TableCell  style={{ minWidth: "100px" }}  align="center">{row.criteria}</TableCell>
                <TableCell align="center">{row.value}</TableCell>
                <TableCell align="center">{row.email}</TableCell>
                <TableCell align="right">{row.activeDays}</TableCell>
                <TableCell style={{ minWidth: "130px" }} align="center">
                  {row.actions}
                </TableCell>
              </TableRow>
            ))}

            {emptyRows > 0 && (
              <TableRow style={{ height: 53 * emptyRows }}>
                <TableCell colSpan={7} />
              </TableRow>
            )}
          </TableBody>
          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[5, 10, 25, { label: "All", value: -1 }]}
                colSpan={7}
                count={rows.length}
                rowsPerPage={rowsPerPage}
                page={page}
                SelectProps={{
                  inputProps: { "aria-label": "rows per page" },
                  native: true,
                }}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
                ActionsComponent={TablePaginationActions}
              />
            </TableRow>
          </TableFooter>
        </Table>
      </TableContainer>
    </div>
  );
}
